* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: poppins, sans-serif;
}

body::-webkit-scrollbar {
  width: 1em;
}
 
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
body::-webkit-scrollbar-thumb {
  background-color: #191919;
  outline: 1px solid #191919;
  
}
::-webkit-scrollbar-thumb:hover {
  background: gray; 
}
::-moz-selection { /* Code for Firefox */
  background: gray;
}

::selection {
  background: gray;
}
a:hover {
  text-decoration: none;
}
a{
  text-decoration: none;
}
.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(490px, 1fr));
}
@media only screen and (max-width: 600px) {
  .grid-container  {
    grid-template-columns: 1fr;
  }
}
.grid-item {
  position: relative;
  height: 700px;
  overflow: hidden;
}
.homeImage {
  display: block;
  height: 100%;
  margin: 0 -50%;
  width: 200%;
}
.overlay {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  background: rgb(2, 2, 4);
  background: linear-gradient(
    180deg,
    rgba(2, 2, 4, 0) 0%,
    rgba(2, 2, 4, 0) 25%,
    rgba(2, 2, 4, 0.9444152661064426) 93%
  );
}
.subContant {
  position: absolute;
  width: 70%;
  right: 15%;
  bottom: 50px;
  color: white;
  text-align: left;
}
.subContant h1 {
  font-family: poppins, sans-serif;
  color: #fff;
  text-transform: none;
  font-size: 53px;
  line-height: 51px;
  font-weight: 700;
}
.grid-item section {
  height: 0px;
  transition-property: height;
  transition-duration: 0.3s;
  overflow: hidden;
}
.grid-item a {
  letter-spacing: -2px;
}
.grid-item a:hover + section {
  height: 130px;
}
.grid-item section:hover {
  height: 130px;
}
